body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.desktopNav {
  background-color: #fff;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.navLogo {
  float: left;
  width: 35%;
}

.logo{
  width: 30%;
  vertical-align: middle;
  margin: 0;
}

.navContent {
  /*position: absolute;
  right: 0;*/
  float: right;
  margin: 50px 90px 0px 0px;
  padding: 5px 0;
}

.navItem{
  color: #000;
  margin: 20px;
  text-decoration: none;
}

.navItem:hover{
  opacity: 0.5;
}

.language{
  text-decoration: none;
  position: absolute;
  top: 5px;
  right: 15px;
}

.language span {
  text-decoration: none;
  font-family: 'Raleway', sans-serif;
  font-weight:600;
  font-size: 11px;
  color: #000;
}

.language span:hover {
  opacity: 0.7;
}
.brief h2 {
  color: #333;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  font-size: 27px;
  text-align: center;
  text-transform: uppercase;
  padding-top: 140px;
}

.brief h3 {
  color: #333;
  font-family: 'Raleway', sans-serif;
  font-weight: 500;
  font-size: 27px;
  text-align: center;
  text-transform: uppercase;
  padding-top: 60px;
}

.brief p {
  padding: 10px 130px;
  font-size: 20px;
  text-align: left;
}
.jumbotron {
  height: 450px;
  background-image: url(/static/media/background.46d4f952.jpg);
  background-size: cover;
}

main {
  text-align: center;
  position: relative;
  top: 100px;
}

main h1 {
  text-align: center;
  color: #fff;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: 70px;
  padding-top: 0px;
  margin-bottom: 0px;
  text-transform: uppercase;
}

.header{
  padding-top: 140px;
}
.newsList {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: 2rem 10% 2rem 10%;
}
.news {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    width: 20rem;
    margin: 0 .5rem 2.3rem .5rem;
    background-color: #eee;
    text-decoration: none;
    color: #000;
}

.news:hover{
    opacity: 0.8;
  }
  

.news .image-container {
    height: 13em;
    margin-bottom: 1rem;
}

.newsContent {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.details {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

.details h4{
    margin: 0px 10px 10px 10px;
    font-size: 1.1rem;
    font-weight: 600;
}

.details h5{
    margin: 0px 10px 10px 10px;
    font-size: 1.1rem;
    font-weight: 300;
}

.details p {
    font-size: .88rem;
    font-weight: 300;
    line-height: 1rem;
    margin: 0px;
}


.serviceList {
  margin: 0px 0 100px 0;
}
.service {
  height: 40%;
  margin: 70px 30px 0px 30px;
  display: inline-block;
  text-align: center;
}

.service div{
  margin-top: 30px;
  display: inline-block;
  vertical-align: middle;
}

.service .image{
  width: 45%;
}

.service img {
  width: 90%;
}

.service .details{
  text-align: left;
  width: 40%;
}

.service h2 {
  font-weight: 600;
  font-size: 27px;
  text-transform: uppercase;
  text-align: center;
}

.service p {
  font-weight: 400;
  font-size: 20px;
  text-align: left;
  line-height: 20px;
  padding: 0 40px;
  margin: 10px 0px 30px 0px;
  width: 90%;
}

.service a {
  background-color: white;
  color: #333333;
  font-family: 'Raleway', sans-serif;
  font-weight: 600;
  font-size: 12px;
  letter-spacing: 1.3px;
  text-decoration: none;
  text-transform: uppercase;
  padding: 10px;
  margin-bottom: 10px;
  border: 2px solid #333333; 
}
footer {
  background-color: #333;
  color: #fff;
  padding: 5px 0px;
  margin-top: 0px
}

footer p {
  font-family: 'Raleway', sans-serif;
  font-size: 15px;
  margin: 10px;
}

footer .row {
  display: inline-block;
  width: 90%;
  height: 70px;
  margin: 20px 10px 0px 10px;
}

hr {
  border-top-color:#fff;
  opacity: 1;
  margin: 50px 50px 0 50px;
}

footer a:hover
{
  color:#3366cc;
  text-decoration:none;
}

.groups{
  /*position: absolute;
  left: 90px;*/
  float: left;
}

.group {
  color: #777;
  display: inline-block;
  width: 100px;
  vertical-align: top;
  text-align: left;
  margin: 0 20px 0 0;
}

.footer-links li{
  
}

.link {
  color: #fff;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  font-size: 14px;
  text-decoration: none;
}

.signup {
  /*position: absolute;
  right: 90px;*/
  float: right;
}

.emailSignup p {
  float: center;
}

.emailCapture .button {
  margin: 10px;
}

.emailCapture .button:hover {
  background: rgb(196, 192, 192);
}

.legal{
  /*position: absolute;
  left: 80px;*/
  float: left;
}

.social {
  /*position: absolute;
  right: 75px;*/
  float: right;
}

.social-icons li
{
  display:inline-block;
}

.social-icons a{
  background-color:#eceeef;
  color: white;
  font-size:16px;
  display:inline-block;
  line-height:44px;
  width:44px;
  height:44px;
  text-align:center;
  margin-right:8px;
  border-radius:100%;
  transition:all .2s linear
}

.social-icons a:hover {
  opacity: 1;
  color:#818a91;
}

.social-icons a.facebook
{
  background-color:#3b5998;
}
.social-icons a.twitter
{
  background-color:#00aced;
}
.social-icons a.instagram
{
  background-color:#ea4c89;
}
.social-icons a.linkedin
{
  background-color:#007bb6
}
html, body {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 940px;
  margin: 0 auto;
  padding: 0 10px;
  text-align: center;
}

/* Reusable Component - Image Container */
.image-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  overflow: hidden;
  background: #000;
}

.image-container img {
  width: 100%;
}

@media (max-width: 900px) {
  .navContent {
    margin: 50px 70px 0px 0px;
  }

  .brief p {
    padding: 10px 50px;
  }

  .service {
    height: 570px;
  }

  .service .image {
    width: 90%;
  }

  .service .details{
    text-align: center;
    width: 90%;
  }

  .service p {
    text-align: center;
    padding: 0px 20px;
  }
}

@media screen and (max-width: 530px) {
  .navLogo{
    width: 100%;
    text-align: center;
  }

  .logo{
    width: 10%;
    float: center;
  }

  .navContent{
    width: 100%;
    margin: 0px;
    text-align: center;
  }

  main {
    top: 70px;
  }

  main h1 {
    font-size: 50px;
  }

  .jumbotron {
    height: 400px;
  }

  .brief p {
    padding: 10px 50px;
  }

  .service {
    height: 400px;
  }

  .service p {
    text-align: center;
    padding: 0px;
  }

  footer {
    height: 330px;
  }

  footer .row {
    display: block;
  }

  footer section {
    width: 100%;
  }

  footer .group {
    margin: 10px;
    text-align: center;
  }

  hr {
    margin: 30px 20px 0px 20px;
  }
}

@media (max-width: 350px) {
  main h1 {
    font-size: 30px;
  }

  .service {
    height: 300px;
  }

  footer {
    height: 60%;
  }

  .social-icons a{
    line-height:30px;
    width:30px;
    height:30px;
  }
}

